"use client"

import { useRouter } from "next/navigation"
import { ChevronLeft } from "lucide-react"

// Dữ liệu hồ sơ trưởng khoa
const truongKhoaData = {
  maTruongKhoa: "00001",
  hoTen: "Nguyễn Văn A",
  gioiTinh: "Nam",
  ngaySinh: "15/08/1982",
  email: "a.nguyen@example.edu",
  soDienThoai: "0987654321",
  hocVi: "Tiến sĩ",
  chucVu: "Trưởng Khoa",
  chuyenNganh: "Trí Tuệ Nhân Tạo & Học Máy",
  khoa: "Khoa Học Máy Tính",
  diaChi: "123 Example St, District 1, Ho Chi Minh City",
  ngayGiaNhap: "20/08/2010",
  tongSoLop: 42,
  trangThai: "Đang hoạt động",
  avatar: "/avatar-user-asian-male.jpg",
}

export default function HoSoTruongKhoaPage() {
  const router = useRouter()

  return (
    <main className="flex-1 p-6">
      <div className="flex items-center gap-4 mb-6">
        <button onClick={() => router.back()} className="text-gray-500 hover:text-gray-700">
          <ChevronLeft className="w-6 h-6" />
        </button>
        <h1 className="text-[#4A90D9] text-2xl font-bold">Hồ Sơ Trưởng Khoa</h1>
      </div>

      <div className="bg-white rounded-xl p-6 shadow-sm">
        <div className="flex gap-8">
          {/* Avatar */}
          <div className="flex-shrink-0">
            <img
              src={truongKhoaData.avatar || "/avatar-user-asian-male.jpg"}
              alt={truongKhoaData.hoTen}
              className="w-32 h-32 rounded-full object-cover border-4 border-gray-100"
            />
          </div>

          {/* Thông tin */}
          <div className="flex-1 grid grid-cols-3 gap-x-12 gap-y-4">
            <div>
              <p className="text-gray-500 text-sm mb-1">Mã Giảng Viên</p>
              <p className="text-gray-800 font-medium">{truongKhoaData.maTruongKhoa}</p>
            </div>
            <div>
              <p className="text-gray-500 text-sm mb-1">Họ và Tên</p>
              <p className="text-gray-800 font-medium">{truongKhoaData.hoTen}</p>
            </div>
            <div>
              <p className="text-gray-500 text-sm mb-1">Giới Tính</p>
              <p className="text-gray-800 font-medium">{truongKhoaData.gioiTinh}</p>
            </div>
            <div>
              <p className="text-gray-500 text-sm mb-1">Ngày Sinh</p>
              <p className="text-gray-800 font-medium">{truongKhoaData.ngaySinh}</p>
            </div>
            <div>
              <p className="text-gray-500 text-sm mb-1">Email</p>
              <p className="text-gray-800 font-medium">{truongKhoaData.email}</p>
            </div>
            <div>
              <p className="text-gray-500 text-sm mb-1">Số Điện Thoại</p>
              <p className="text-gray-800 font-medium">{truongKhoaData.soDienThoai}</p>
            </div>
            <div>
              <p className="text-gray-500 text-sm mb-1">Học Vị/Chức Danh</p>
              <p className="text-gray-800 font-medium">{truongKhoaData.hocVi}</p>
            </div>
            <div>
              <p className="text-gray-500 text-sm mb-1">Chức Vụ</p>
              <p className="text-gray-800 font-medium">{truongKhoaData.chucVu}</p>
            </div>
            <div>
              <p className="text-gray-500 text-sm mb-1">Khoa</p>
              <p className="text-gray-800 font-medium">{truongKhoaData.khoa}</p>
            </div>
            <div>
              <p className="text-gray-500 text-sm mb-1">Chuyên Ngành</p>
              <p className="text-gray-800 font-medium">{truongKhoaData.chuyenNganh}</p>
            </div>
            <div>
              <p className="text-gray-500 text-sm mb-1">Địa Chỉ</p>
              <p className="text-gray-800 font-medium">{truongKhoaData.diaChi}</p>
            </div>
            <div>
              <p className="text-gray-500 text-sm mb-1">Tổng Số Lớp Giảng Dạy</p>
              <p className="text-gray-800 font-medium">{truongKhoaData.tongSoLop}</p>
            </div>
            <div>
              <p className="text-gray-500 text-sm mb-1">Ngày Gia Nhập</p>
              <p className="text-gray-800 font-medium">{truongKhoaData.ngayGiaNhap}</p>
            </div>
            <div>
              <p className="text-gray-500 text-sm mb-1">Trạng Thái</p>
              <span className="inline-block px-3 py-1 rounded-full text-xs font-medium bg-green-100 text-green-600">
                {truongKhoaData.trangThai}
              </span>
            </div>
          </div>
        </div>
      </div>
    </main>
  )
}

